<template>
  <Modal title="" :modelValue="visible" :mask-closable="false" :width="700" ok-text="" @on-cancel="onCancel" @on-ok="finish('blob')">
    <Row>
      <Col :xs="24" :md="12" style="height: 350px">
        <VueCropper
          ref="cropper"
          :img="options.img"
          :autoCrop="options.autoCrop"
          :autoCropWidth="options.autoCropWidth"
          :autoCropHeight="options.autoCropHeight"
          :fixedBox="options.fixedBox"
          :original="options.original"
          :centerBox="options.centerBox"
          @realTime="realTime"
        />
      </Col>
      <Col :xs="24" :md="12" style="height: 350px">
        <div class="avatar-cropper-preview">
          <img :src="previews.url" :style="previews.img" />
        </div>
      </Col>
    </Row>
    <br />
    <Row>
      <Col :xs="24" :md="12">
        <Row>
          <Col span="12">
            <Upload name="file" action="action" accept=".jpeg,.jpg,.png" :before-upload="beforeUpload" :show-upload-list="false">
              <Button icon="ios-camera"></Button>
            </Upload>
          </Col>
          <Col span="12" class="avatar-action">
            <Tooltip content="" theme="light" placement="top" transfer>
              <Button icon="md-add" @click="changeScale(1)" />
            </Tooltip>
            <Tooltip content="" theme="light" placement="top" transfer>
              <Button icon="md-remove" @click="changeScale(-1)" />
            </Tooltip>
            <Tooltip content="" theme="light" placement="top" transfer>
              <Button icon="md-refresh" @click="rotateRight" />
            </Tooltip>
          </Col>
        </Row>
      </Col>
    </Row>
  </Modal>
</template>
<script>
  import 'vue-cropper/dist/index.css'
  import { VueCropper } from 'vue-cropper'

  export default {
    name: 'AvatarCropper',
    components: { VueCropper },
    props: {
      avatarPath: {
        type: String,
        default: '',
        required: true
      }
    },
    data() {
      return {
        visible: false,
        uploading: false,
        options: {
          /**
           *  @see https://github.com/xyxiao001/vue-cropper
           */
          img: '', // 
          autoCrop: true, // 
          autoCropWidth: 200, // 
          autoCropHeight: 200, // 
          fixed: true, // 
          fixedBox: true, // 
          centerBox: false, // 
          original: false // 
        },
        previews: {}
      }
    },
    methods: {
      edit() {
        this.options.img = this.avatarPath
        this.visible = true
      },
      onCancel() {
        this.options.img = ''
        this.visible = false
      },
      changeScale(num) {
        num = num || 1
        this.$refs.cropper.changeScale(num)
      },
      rotateLeft() {
        this.$refs.cropper.rotateLeft()
      },
      rotateRight() {
        this.$refs.cropper.rotateRight()
      },
      beforeUpload(file) {
        const reader = new FileReader()
        // Array Bufferblob base64
        // base64
        reader.readAsDataURL(file)
        reader.onload = () => {
          this.options.img = reader.result
        }
        // blob
        // reader.readAsArrayBuffer(file)

        return false
      },

      // 
      finish(type) {
        const formData = new FormData()
        // 
        if (type === 'blob') {
          this.$refs.cropper.getCropBlob(data => {
            const img = window.URL.createObjectURL(data)
            this.model = true
            this.modelSrc = img
            formData.append('file', data, this.fileName)
            this.$axios
              .post('https://www.mocky.io/v2/5cc8019d300000980a055e76', formData, {
                contentType: false,
                processData: false,
                headers: {
                  'Content-Type': 'application/x-www-form-urlencoded'
                }
              })
              .then(response => {
                console.log('upload response:', response)
                // var res = response.data
                // if (response.status === 'done') {
                //   _this.imgFile = ''
                //   _this.headImg = res.realPathList[0] // 
                //   _this.uploadImgRelaPath = res.relaPathList[0] // 
                //   _this.$Message.success('')
                //   this.visible = false
                // }
                this.$Message.success('')
                this.$emit('ok', response.url)
                this.visible = false
              })
          })
        } else {
          this.$refs.cropper.getCropData(data => {
            this.model = true
            this.modelSrc = data
          })
        }
      },
      realTime(data) {
        this.previews = data
      }
    }
  }
</script>

<style lang="less" scoped>
  .avatar-cropper-preview {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 200px;
    height: 200px;
    border-radius: 50%;
    box-shadow: 0 0 4px #ccc;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
    }
  }

  .avatar-action {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
</style>
